.gpt3__features-container__feature {
    align-items: flex-start;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin: 1rem;
    width: 100%;
}

.gpt3__features-container__feature-title{
    flex: 1;
    margin-bottom: 10px;
    margin-right: 2rem;
    max-width: 180px;
}

.gpt3__features-container__feature-title h1 {
    color: #FFFFFF;
    font-family: var(--font-family);
    font-size: 18px;
    font-weight: 800;
    letter-spacing: -0.04em;
    line-height: 24px;
}

.gpt3__features-container__feature-title div {
    background: var(--gradient-bar);
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
    height: 3px;
    margin-bottom: 0.25rem;
    width: 38px;
}

.gpt3__features-container__feature-text{
    display: flex;
    flex: 2;
    max-width: 390px;
}

.gpt3__features-container__feature-text p{
    color: var(--color-text);
    font-family: var(--font-family);
    font-size: 14px;
    font-weight: 500;
    line-height: 24px;
}

@media screen and (max-width: 550px) {
    .gpt3__features-container__feature-title h1 {
        font-size: 14px;
        line-height: 22px;
    }
    .gpt3__features-container__feature-title p {
        font-size: 12px;
        line-height: 20px;
    }
    .gpt3__features-container__feature{
        margin: 1rem 0;
    }
}