.gpt3__whatgpt3 {
    display: flex;
    flex-direction: column;

    padding: 2rem;
    /* s */

    /* ff 3.6+ */
    /* background:-moz-radial-gradient(circle at 30% -100%, #042c54 25%, rgba(4, 44, 84, 1) 85%, rgba(27, 120, 222, 1) 100%); */
    background: rgb(5,11,75);
    background: linear-gradient(90deg, rgba(5,11,75,0.999019676229867) 0%, rgba(77,175,208,0.8645658947172619) 100%);
    /* safari 5.1+,chrome 10+ */
    /* background:-webkit-radial-gradient(circle at 30% -100%, #042c54 25%, rgba(4, 44, 84, 1) 85%, rgba(27, 120, 222, 1) 100%); */

    /* opera 11.10+ */
    /* background:-o-radial-gradient(circle at 30% -100%, #042c54 25%, rgba(4, 44, 84, 1) 85%, rgba(27, 120, 222, 1) 100%); */

    /* ie 10+ */
    /* background:-ms-radial-gradient(circle at 30% -100%, #042c54 25%, rgba(4, 44, 84, 1) 85%, rgba(27, 120, 222, 1) 100%); */

    /* global 92%+ browsers support */
    /* background:radial-gradient(circle at 30% -100%, #042c54 25%, rgba(4, 44, 84, 1) 85%, rgba(27, 120, 222, 1) 100%); */

    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}

.gpt3__whatgpt3-feature {
    display: flex;
}

/* Customizing feature component as per needs */
.gpt3__whatgpt3-feature 
.gpt3__features-container__feature {
    margin: 0;
}

/* Customizing feature component as per needs */
.gpt3__whatgpt3-feature 
.gpt3__features-container_feature-text {
    max-width: 700px;
}

.gpt3__whatgpt3-Languages{
    display: flex;
}
.gpt3__whatgpt3-Languages button{
    background-color: transparent;
    border-color: #fff;
    border-radius: 8px;
    color: white;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    font-family: var(--font-family);
    margin-left: 10px;
    margin-right: 10px;
    padding-left: 10px;
    padding-right: 10px;
    padding: 5px;
}

.gpt3__whatgpt3-Languages button:hover{
    color: rgb(38, 223, 183);
    border-radius: 16px;
}

.gpt3__whatgpt3-heading {
    align-items: center;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin: 4rem 0 2rem;
}

.gpt3__whatgpt3-heading h1 {
    font-family: var(--font-family);
    font-size: 34px;
    font-weight: 800;
    line-height: 45px;
    max-width: 510px;
}

.gpt3__whatgpt3-heading p {
    color: var(--color-subtext);
    cursor: pointer;
    font-family: var(--font-family);
    font-size: 16px;
    font-weight: 500;
    line-height: 30px;
}

.gpt3__whatgpt3-container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin-top: 2rem;
}

.gpt3__whatgpt3-container 
.gpt3__features-container__feature {
    display: unset;
    flex-direction: column;
    flex: 1;
    margin: 1rem;
    min-width: 210px;
}

.gpt3__whatgpt3-container 
.gpt3__features-container_feature-text {
    margin-top: 0.5rem;
}

@media screen and (max-width: 850px) {
    .gpt3__whatgpt3-heading {
        align-items: flex-start;
        flex-direction: column;
        margin-bottom: 0rem;
    }

    .gpt3__whatgpt3-heading p {
        margin-top: 1rem;
    }
}

@media screen and (max-width: 650px) {
    /* Customizing feature component as per needs */
    .gpt3__whatgpt3-feature .gpt3__features-container__feature {
        flex-direction: column;
    }

    /* Customizing feature component as per needs */
    .gpt3__whatgpt3-feature .gpt3__features-container_feature-text {
        margin-top: 0.5rem;
    }

    .gpt3__whatgpt3-heading h1 {
        font-size: 28px;
        line-height: 40px;
    }
}

@media screen and (max-width: 350px) {
    /* Customizing feature component as per needs */
    .gpt3__whatgpt3-container .gpt3__features-container__feature {
        margin: 1rem 0;
        min-width: 100%;
    }
}